<template>
  <div id="message" :class="{hasHeader: $utils.showNavBar()}">
    <HeaderNavBar v-if="$utils.showNavBar()"></HeaderNavBar>
    <div class="dropDown" :class="[`dropDown${ramdomIndex}`, {isBottom: topscroll >= 90}]"  :style="{'margin-top':topscroll +'px','transform':'rotateZ('+ topscroll*25+'deg)'}"></div>
    <div class="wrapper" ref="wrapper">
      <div class="mes">
        <transition name="topTxt">
          <div class="text-center lh66 topTxt" v-if="topBol">
            {{num!==0?""+ (num ? num : 0) +"":"~"}}
          </div>
        </transition>
        <div class="nullImg text-center f30" v-if="list.length === 0"><span></span></div>
        <div class="scrollList">
          <div class="mesList" @click="toPage" v-for="(item,index) in list" :key="index">
            <div v-if="!item.type">
              <div class="list-titleTime text-center f24">{{$utils.timestampToStr(item.create_time * 1000, 'Y/M/D h:m:s')}}</div>
              <div class="list-cont flex">
                <div class="list-head-pr"><img :src="item.from_avatar" alt=""></div>
                <div class="list-text f28" v-html="item.content"></div>
              </div>
            </div>
            <div class="scrollTo" ref='scrollTo' v-else></div>
          </div>
          <div class="isLoading no-more-data" v-show="isLoading">...</div>
        </div>
      </div>
    </div>
    <img-preview v-if="showPreview" @closeFn="showPreview = false" :imgs="imgs" :currentIndex="currentIndex"></img-preview>
  </div>
</template>
<script>
import Vue from 'vue'
import HeaderNavBar from '@/components/task/header-nav'
import { InfiniteScroll } from 'mint-ui'
import {mapState, mapMutations} from 'vuex'
import BScroll from 'better-scroll'
import ImgPreview from './imgPreview'
Vue.use(InfiniteScroll)
export default {
  data () {
    return {
      showPreview: false,
      currentIndex: 0,
      conten: [],
      page: 1,
      imgs: [],
      loading: false,
      isLoading: false,
      ramdomIndex: 0,
      topBol: false,
      num: 0,
      topscroll: 0,
      list: [],
      complete: false,
      pos: {},
      tobottom: false
    }
  },
  beforeRouteLeave (to, from, next) {
    if (this.$preview.self) {
      this.$preview.self.close()
      this.$preview.self = null
      next(false)
    } else {
      next()
    }
  },
  mounted () {
    this.$indicator.open({
      text: ''
    })
    this.$preview.on('close', res => {
      this.$preview.self = null
    })
    this.loadData()
    this.getImgs()
    this.getList(res => {
      res.data.data = res.data.data.reverse()
      this.list = res.data.data
      this.resetMessage('all')
      this.tobottom = 1
    })
  },
  computed: mapState({
    commonState: state => state.common
  }),
  methods: {
    ...mapMutations(['common_set_hasRed']),
    // 
    getImgs () {
      this.$refs.wrapper.addEventListener('click', (e) => {
        e = e || window.event
        let el = e.target || e.srcElement
        if (el.className === 'messageImage') {
          // this.getAllImg(el.src)
        }
      }, {passive: false})
    },
    // 
    getAllImg () {
      let imgs = this.$refs.wrapper.querySelectorAll('.messageImage')
      let imgUrls = []
      for (let img of imgs) {
        imgUrls.push(img.getAttribute('src'))
      }
      this.imgs = imgUrls
      this.showPreview = true
    },
    // 
    resetMessage (msgId) {
      let data = {
        msg_id: msgId
      }
      if (this.commonState.login_bol && this.commonState.hasRed['/personal_center']) {
        this.$api.post('MESSAGE_READ', data, res => {
          // 
          this.commonState.hasRed['/personal_center'] = 0
          // this.common_set_hasRed(this.commonState.hasRed)
        })
      }
    },
    back () {
      this.$router.go(-1)
    },
    getList (cb) {
      setTimeout(() => {
        this.isLoading = false
        this.scroll && this.scroll.finishPullDown()
      }, 2000)
      this.$api.get('MESSAGE_LIST', {page: this.page}, res => {
        typeof cb === 'function' && cb(res)
        this.isLoading = false
        if (res.data.last_page <= this.page) {
          this.loading = true
          this.complete = true
        } else {
          this.page++
        }
        this.$indicator.close()
        this.$nextTick(() => {
          this.loadData()
          this.scroll && this.scroll.finishPullDown()
        })
      }, res => {
        this.isLoading = false
        this.scroll && this.scroll.finishPullDown()
      })
    },
    toPage (e) {
      e = e || window.event
      let el = e.target || e.srcElement
      let url = el.getAttribute('data-url')
      this.toUrl(url)
    },
    toUrl (url) {
      if (!url) return
      if (url.indexOf('http') > -1) {
        if (this.$config.source_style >= 6) {
          window.plus.runtime.openURL(url)
        } else {
          window.open(url, '_self')
        }
      } else if (url.indexOf('#') > -1) {
        this.$router.push(url.replace('#', ''))
      }
    },
    // 
    downlist () {
      if (this.complete) {
        setTimeout(() => {
          this.num = 0
          this.topBol = true
          setTimeout(() => {
            this.topBol = false
            this.loadData()
          }, 2000)
        }, 400)
        return
      }
      if (this.topBol) {
        this.isLoading = false
        return
      }
      if (this.isLoading) {
        return
      }
      if (this.timer) {
        clearTimeout(this.timer)
        this.timer = null
      }
      this.isLoading = true
      this.timer = setTimeout(() => {
        this.isLoading = true
        this.getList(res => {
          this.num = res.data.data.length
          res.data.data = res.data.data.reverse()
          this.list = [...res.data.data, {type: 999}, ...this.list]
          this.tobottom = 2
          if (res.data.length === 0) {
            this.tobottom = 0
          }
          this.topBol = true
          setTimeout(() => {
            this.topBol = false
            this.loadData()
          }, 2000)
        })
      }, 400)
    },
    loadData () {
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.wrapper, {
            click: true,
            tap: true,
            probeType: 3,
            pullDownRefresh: {
              threshold: 50,
              stop: 0
            },
            bounceTime: 400,
            bounce: true,
            mouseWheel: {
              speed: 10,
              invert: false,
              easeTime: 300
            },
            preventDefaultException: {
              className: /messageImage/
            },
            scrollbar: {
              fade: true,
              interactive: false
            }
          })
          this.scroll.on('scroll', pos => {
            if (pos.y < 100 && pos.y > -100) {
              if (!this.topBol) {
                this.topscroll = pos.y
                if (pos.y === 0) {
                  this.ramdomIndex = Math.round(Math.random() * 10)
                }
              }
            }
          })
          this.scroll.on('pullingDown', () => {
            this.downlist()
          })
        } else {
          if (this.tobottom === 1) {
            setTimeout(() => {
              this.scroll.refresh()
              this.tobottom = 0
              this.scroll.scrollTo(0, this.scroll.maxScrollY)
              this.$previewRefresh()
            }, 200)
          } else if (this.tobottom === 2) {
            if (this.$refs.scrollTo) {
              setTimeout(() => {
                this.scroll.refresh()
                this.scroll.scrollToElement(this.$refs.scrollTo[0])
                this.tobottom = 0
                this.$previewRefresh()
              }, 200)
            }
          } else {
            setTimeout(() => {
              this.$previewRefresh()
              this.scroll.refresh()
            }, 200)
          }
        }
      })
    }
  },
  components: {
    HeaderNavBar,
    ImgPreview
  },
  filters: {
    newCreat (time) {
      let Ntime = new Date(time)
      let y
      let r
      let h
      let s
      y = (Ntime.getMonth() + 1) > 9 ? (Ntime.getMonth() + 1) : '0' + (Ntime.getMonth() + 1)
      r = Ntime.getDate() > 9 ? Ntime.getDate() : '0' + Ntime.getDate()
      h = Ntime.getHours() > 9 ? Ntime.getHours() : '0' + Ntime.getHours()
      s = Ntime.getMinutes() > 9 ? Ntime.getMinutes() : '0' + Ntime.getMinutes()
      return  y + '-' + r + ' ' + h + ':' + s
    }
  }
}
</script>
<style lang="scss" scoped>
#message {
  height: 100vh;
  box-sizing: border-box;
  overflow: auto;
  background: #f5f5f5;
  position: relative;
  &.hasHeader {
    .wrapper {
      height: calc(100% - 88px);
    }
    .dropDown {top: 0}
  }
  .wrapper {
    height: calc(100% - 1px);
    overflow: hidden;
    position: relative;
    .topTxt{
      background: #FCEAC5;
      height: 66px;
      overflow: hidden;
      color: #fa0;
    }
    .scrollList {
      padding-top: 20px;
      padding-bottom: 30px;
      overflow: hidden;
    }
  }
}
.dropDown{
  position: absolute;
  top: -100px;
  z-index: 3;
  width: 50px;
  height:50px;
  background: url('../../../assets/img/down-top.png') no-repeat;
  background-size:500px;
  border-radius: 50%;
  left: 50%;
  margin-left: -30px;
  transform-origin: center;
  background-position: -252.5px -178.5px;
  &.isBottom {
    transform: rotateZ(0deg)!important
  }
}
.dropDown0{background-position: -19.5px -18.5px}
.dropDown1{background-position: -97.7px -18.5px}
.dropDown2{background-position: -173.5px -20.5px}
.dropDown3{background-position: -252px -18.5px}
.dropDown4{background-position: -333.5px -19.5px}
.dropDown5{background-position: -409.5px -18.5px}

.dropDown6{background-position: -20px -104.5px}
.dropDown7{background-position: -98px -104px}
.dropDown8{background-position: -177px -104px}
.dropDown9{background-position: -252.5px -104.5px}
.dropDown10{background-position: -332.5px -103.8px}
.dropDown11{background-position: -411.5px -104.5px}

.dropDown12{background-position: -20px -177px}
.dropDown13{background-position: -97.5px -178.5px}
.dropDown14{background-position: -177px -179.5px}
.dropDown15{background-position: -252.5px -178.5px}
.dropDown16{background-position: -332.5px -177.5px}
.dropDown17{background-position: -409.5px -178px}
.mes{
  font-size: 30px;
  color: #232323;
  margin: 0 auto;
  .nullImg{
    span{
      display: block;
      margin: auto;
      width: 182px;
      height: 182px;
      background: url("../../../assets/img/my/nullMess.png") no-repeat;
      background-size: 100%;
      margin-top: 173px;
      margin-bottom: 92px;
    }
  }
}
.list-titleTime{
  width: 282px;
  height: 40px;
  line-height: 40px;
  background: rgba(0,0,0,0.3);
  color: #fff;
  border-radius: 40px;
  margin: 40px auto;
}
.list-cont.flex {
  padding-left: 38px;
  box-sizing: border-box;
  .list-head-pr {
    width: 55px;
    height: 55px;
    border-radius: 50%;
    overflow: hidden;
    margin-top: 12px;
    img{
      width: 100%;
      height: 100%;
    }
  }
  .list-text {
    margin-left: 24px;
    width: 475px;
    background: #fff;
    border-radius: 10px;
    border-top-left-radius: 0;
    box-sizing: border-box;
    padding: 15px;
    line-height: 1.5;
    color: #666;
    overflow: visible;
    word-break: break-all;
    position: relative;
    &::after{
      content: '';
      display: block;
      position: absolute;
      left: -14px;
      top: 20px;
      width: 26px;
      height: 40px;
      background: url('../../../assets/img/my/message_arrow.png') no-repeat center;
      background-size: 100%;
      z-index: 2;
    }
  }
}
</style>



