
import Cropper from 'cropperjs'
export default {
  install (Vue) {
    // 
    Vue.prototype.initilize = function (opt) {
      let self = this
      this.options = opt
      // dom
      this.createElement()
      this.resultObj = opt.resultObj
      // 
      this.cropper = new Cropper(this.preview, {
        aspectRatio: opt.aspectRatio || 1,
        autoCropArea: opt.autoCropArea || 0.8,
        viewMode: 1,
        guides: opt.aspectRatio === 'Free' ? '' : true,
        cropBoxResizable: opt.aspectRatio === 'Free' ? '' : true,
        cropBoxMovable: opt.aspectRatio === 'Free' ? '' : true,
        dragCrop: opt.aspectRatio === 'Free' ? '' : true,
        background: false,
        checkOrientation: true,
        checkCrossOrigin: true,
        zoomable: false,
        zoomOnWheel: false,
        center: false,
        toggleDragModeOnDblclick: false,
        ready: function () {
          if (opt.aspectRatio === 'Free') {
            let cropBox = self.cropper.cropBox
            cropBox.querySelector('span.cropper-view-box').style.outline = 'none'
            self.cropper.disable()
          }
        }
      })
    }
    // DOM
    Vue.prototype.createElement = function () {
      // 
      this.preview = null
      let str = '<div><img id="clip_image" src="originUrl"></div><button type="button" id="cancel_clip"></button><button type="button" id="clip_button"></button>'
      str += '<div class="crop_loading"><div class="crop_content"><div class="rotate"></div><div class="crop_text"></div></div></div>'
      str += '<div class="crop_success"><div class="crop_success_text"></div></div></div>'
      let body = document.getElementsByTagName('body')[0]
      this.reagion = document.createElement('div')
      this.reagion.id = 'clip_container'
      this.reagion.className = 'container'
      this.reagion.innerHTML = str
      // DOM
      body.appendChild(this.reagion)
      this.preview = document.getElementById('clip_image')
      // 
      this.initFunction()
    }
    // 
    Vue.prototype.initFunction = function  () {
      let self = this
      this.clickBtn = document.getElementById('clip_button')
      this.cancelBtn = document.getElementById('cancel_clip')
      // 
      this.addEvent(this.clickBtn, 'click', function () {
        self.crop()
      })
      // 
      this.addEvent(this.cancelBtn, 'click', function () {
        self.destoried()
      })
      // input
      this.addEvent(this.fileObj, 'click', function () {
        this.value = ''
      })
    }
    // input['file']change
    Vue.prototype.clip = function (e, opt) {
      // let self = this
      this.fileObj = e.srcElement
      let files = e.target.files || e.dataTransfer.files
      if (!files.length) return false  // 
      // 
      this.initilize(opt)
      // 
      this.picValue = files[0]
      // url
      this.originUrl = this.getObjectURL(this.picValue)
      // url
      if (this.cropper) {
        this.cropper.replace(this.originUrl)
      }
    }
    // 
    Vue.prototype.getObjectURL = function (file) {
      let url = null
      if (window.createObjectURL !== undefined) { // basic
        url = window.createObjectURL(file)
      } else if (window.URL !== undefined) { // mozilla(firefox)
        url = window.URL.createObjectURL(file)
      } else if (window.webkitURL !== undefined) { // webkit or chrome
        url = window.webkitURL.createObjectURL(file)
      }
      return url
    }
    // 
    Vue.prototype.crop  = function () {
      let self = this
      let image = new Image()
      let croppedCanvas
      let roundedCanvas
      // Crop
      document.querySelector('.crop_loading').style.display = 'block'
      setTimeout(function () {
        croppedCanvas = self.cropper.getCroppedCanvas()
        // Round
        roundedCanvas = self.getRoundedCanvas(croppedCanvas)
        let imgData = roundedCanvas.toDataURL()
        image.src = imgData
        // 100k,
        if (imgData.length < (100 * 1024)) {
          self.resultObj.src = imgData
          // 
          self.postImg(imgData)
        } else {
          image.onload = function () {
            // 
            let data = self.compress(image, self.Orientation)
            self.resultObj.src = data
            // 
            self.postImg(data)
          }
        }
      }, 20)
    }
    // 
    Vue.prototype.getRoundedCanvas = function (sourceCanvas) {
      let canvas = document.createElement('canvas')
      let context = canvas.getContext('2d')
      let width = sourceCanvas.width
      let height = sourceCanvas.height
      canvas.width = width
      canvas.height = height
      context.imageSmoothingEnabled = true
      context.drawImage(sourceCanvas, 0, 0, width, height)
      context.globalCompositeOperation = 'destination-in'
      context.beginPath()
      context.rect(0, 0, width, height)
      context.fill()
      return canvas
    }
    // 
    Vue.prototype.destoried = function () {
      // let self = this
      // 
      this.removeEvent(this.clickBtn, 'click', null)
      this.removeEvent(this.cancelBtn, 'click', null)
      this.removeEvent(this.fileObj, 'click', null)
      // 
      this.reagion.parentNode.removeChild(this.reagion)

      // 
      this.cropper.destroy()
      this.cropper = null
    }
    // 
    Vue.prototype.postImg = function (imageData) {
      // 
      let self = this
      let file = this.$utils.dataURLtoFile(imageData, new Date().getTime())
      this.$utils.uploader(this.$utils.cache('user_token'), 'avatar', file).then(res => {
        this.newIMG(res.url)
      })
      self.destoried()
    }

    // 
    Vue.prototype.rotateImg = function (img,  direction, canvas) {
      // 4
      const minStep = 0
      const maxStep = 3
      if (img == null) return
      // imgimg
      let height = img.height
      let width = img.width
      let step = 2
      if (step == null) {
        step = minStep
      }
      if (direction === 'right') {
        step++
        // 
        step > maxStep && (step = minStep)
      } else {
        step--
        step < minStep && (step = maxStep)
      }
      // 
      let degree = step * 90 * Math.PI / 180
      let ctx = canvas.getContext('2d')
      switch (step) {
        case 0:
          canvas.width = width
          canvas.height = height
          ctx.drawImage(img, 0, 0)
          break
        case 1:
          canvas.width = height
          canvas.height = width
          ctx.rotate(degree)
          ctx.drawImage(img, 0, -height)
          break
        case 2:
          canvas.width = width
          canvas.height = height
          ctx.rotate(degree)
          ctx.drawImage(img, -width, -height)
          break
        case 3:
          canvas.width = height
          canvas.height = width
          ctx.rotate(degree)
          ctx.drawImage(img, -width, 0)
          break
      }
    }

    // 
    Vue.prototype.compress = function (img, Orientation) {
      let canvas = document.createElement('canvas')
      let ctx = canvas.getContext('2d')
      // canvas
      let tCanvas = document.createElement('canvas')
      let tctx = tCanvas.getContext('2d')
      let initSize = img.src.length
      let width = img.width
      let height = img.height

      // 400
      let ratio
      if ((ratio = width * height / 4000000) > 1) {
        ratio = Math.sqrt(ratio)
        width /= ratio
        height /= ratio
      } else {
        ratio = 1
      }
      canvas.width = width
      canvas.height = height
      //        
      ctx.fillStyle = '#fff'
      ctx.fillRect(0, 0, canvas.width, canvas.height)
      // 100
      let count
      if ((count = width * height / 1000000) > 1) {
        count = ~~(Math.sqrt(count) + 1) // 
        //            
        let nw = ~~(width / count)
        let nh = ~~(height / count)
        tCanvas.width = nw
        tCanvas.height = nh
        for (let i = 0; i < count; i++) {
          for (let j = 0; j < count; j++) {
            tctx.drawImage(img, i * nw * ratio, j * nh * ratio, nw * ratio, nh * ratio, 0, 0, nw, nh)
            ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh)
          }
        }
      } else {
        ctx.drawImage(img, 0, 0, width, height)
      }
      // ios 
      if (Orientation !== '' && Orientation !== 1) {
        switch (Orientation) {
          case 6:// 90
            this.rotateImg(img, 'left', canvas)
            break
          case 8:// 90
            this.rotateImg(img, 'right', canvas)
            break
          case 3:// 180
            this.rotateImg(img, 'right', canvas) // 
            this.rotateImg(img, 'right', canvas)
            break
        }
      }
      // 
      let ndata = canvas.toDataURL('image/jpeg', 0.1)
      console.log('' + ~~(100 * (initSize - ndata.length) / initSize) + '%')
      tCanvas.width = tCanvas.height = canvas.width = canvas.height = 0
      return ndata
    }
    // 
    Vue.prototype.addEvent = function (obj, type, fn) {
      if (obj.addEventListener) {
        obj.addEventListener(type, fn, false)
      } else {
        obj.attachEvent('on' + type, fn)
      }
    }
    // 
    Vue.prototype.removeEvent = function (obj, type, fn) {
      if (obj.removeEventListener) {
        obj.removeEventListener(type, fn, false)
      } else {
        obj.detachEvent('on' + type, fn)
      }
    }
  }
}



