<template>
  <div class="maskBox" style="z-index: 6" @click="closeFn">
    <div class="close"></div>
    <swiper ref="swiper" class="swiper">
      <swiper-slide class="swiperSlide" v-for="(item, index) in imgs" :key="index">
        <div class="iconSize">
          <img :src="item" alt="">
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>
<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  components: {
    swiper,
    swiperSlide
  },
  props: {
    imgs: {
      type: Array
    },
    currentIndex: 0
  },
  data () {
    return {
      swipeOptions: {

      },
      index: 0
    }
  },
  methods: {
    closeFn () {
      this.$emit('closeFn', {action: 'close'})
    }
  },
  computed: {
    swiperPreview () {
      return this.$refs.swiper
    }
  }
}
</script>
<style lang="scss" scoped>
.maskBox {
  background: #000;
  .swiper {
    width: 100%;
    background: none;
    height: 100%;
    .swiperSlide {
      position: relative;
      .iconSize {
        position: absolute;
        width: 100%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }
}
</style>



