<template>
<div class="scrollBox">
  <HeaderNavBar v-if="$utils.showNavBar()"></HeaderNavBar>
  <div id="userInfo" :class="{hasHeader: $utils.showNavBar(), isAndroid: $utils.isAndroid()}">
    <div class="conten">
      <div class="content" v-if="userInfo">
        <div class="item-list f30 flex" @click="showGender('headPort')">
          <div class="title-name"></div>
          <div class="item-xian text-right flex-row">
            <label for="photo"></label>
            <div class="headerIcon iconSize" v-if="userInfo.avatar">
              <img :src="userInfo.avatar" alt="">
            </div>
            <span v-else></span>
            <span class="icot">
              <div class="arrowEl"></div>
            </span>
            <img src="" alt="" id="img" >
            <input type="file" id="photo" accept="image/*" @change="change($event)" >
          </div>
        </div>
        <div class="item-list f30 flex border-bottom"  @click="showGender('userName')">
          <div class="title-name"></div>
          <div class="item-xian text-right">
            <span>{{userInfo.nickname ? userInfo.nickname: ''}}</span>
            <span class="icot">
              <div class="arrowEl"></div>
            </span>
          </div>
        </div>
        <div class="rect"></div>
        <div class="item-list f30 flex" @click="showGender('birthdayToggle')">
          <div class="title-name"></div>
          <div class="item-xian text-right">
            <span>{{userInfo.birthday ? userInfo.birthday : ''}}</span>
            <span class="icot">
              <div class="arrowEl"></div>
            </span>
          </div>
        </div>
        <div class="item-list f30 flex gender" @click="showGender('genderToggle')">
          <div class="title-name"></div>
          <div class="item-xian text-right">
            <span v-if="userInfo.sex === 1"></span>
            <span v-else-if="userInfo.sex === 2"></span>
            <span v-else></span>
            <span class="icot">
              <div class="arrowEl"></div>
            </span>
          </div>
        </div>
      </div>
      <div class="item-list f30 flex">
        <div class="title-name"></div>
        <div class="item-xian text-right" @click="bindPhone">
          <span>{{userInfo.mobile ? userInfo.mobile : ''}}</span>
          <span class="icot">
            <div  v-if="!userInfo.mobile" class="arrowEl"></div>
          </span>
        </div>
      </div>
      <div class="item-list f30 flex border-bottom" :class="{active: !userInfo.wechat_nickname, isWx: $config.source >= 6 || $utils.isWx()}">
          <div class="title-name"></div>
          <div class="item-xian text-right flex-row" @click="bindWechat">
            <div class="headerIcon wechat" v-if="userInfo.wechat_avatar">
              <!-- <img :src="userInfo.wechat_avatar" alt=""> -->
              <img src="../../../assets/img/task/person_wechat.png" alt="">
            </div>
            <span class="wechat_name">{{userInfo.wechat_nickname ? userInfo.wechat_nickname : ''}}</span>
            <span class="icot">
              <div class="arrowEl" v-if='!userInfo.wechat_nickname'></div>
            </span>
          </div>
      </div>
      <!--  -->
      <div class="rect"></div>
      <div class="item-list f30 flex" @click="jumpmygrade">
        <div class="title-name"></div>
          <div class="item-xian text-right flex-row">
            <span class="icot">
              <div class="arrowEl"></div>
            </span>
          </div>
      </div>
    </div>
    <!---->
    <div class="userName" style="z-index: 5" v-if="userName">
      <div class="input"><input type="text" v-model="nicknames" placeholder="" maxlength="15"></div>
      <div class="clickList flex text-center f30">
        <div class="toggle" @click="userName=false"></div>
        <div class="toggle" @click="userNameS(nicknames)"></div>
      </div>
    </div>
    <div class="model" style="z-index: 4" v-if="userName" @click="userName=false"></div>
    <!---->
    <div class="genderToggle f30 text-center"  style="z-index: 4" :class="{toggles:genderToggle?'toggles':''}">
      <div class="headPort-tile"></div>
      <div class="photograph " @click="gender(1)" :class="{toggle:userInfo.sex===1?'toggle':''}"></div>
      <div class="picture " @click="gender(2)" :class="{toggle:userInfo.sex===2?'toggle':''}"></div>
    </div>
    <div class="model" v-if="genderToggle" @click="genderToggle=false"></div>
    <div class="titleSucceed f30 text-center" v-if="succeed">{{msgS}}</div>
    <ConnectWechat v-if="showWechat" style="z-index: 10" @closeWechat='closeWechat'></ConnectWechat>
    <confirmBox v-if="showConfirmBox" @closeConfirmBox='closeConfirmBox' :config='configText'></confirmBox>
    <bindPhone style="z-index:15" v-if="showBindPhone" :errChange='errChange' :phoneParams='{title: "", confirm: ""}' @closeBindPhone="closeBindPhone"></bindPhone>
    <ChooseAccount v-if="showChoose" :config='configText' @closeChoose='closeChoose'></ChooseAccount>
  </div>
</div>
  
</template>
<script>
import HeaderNavBar from '@/components/task/header-nav'
import ConnectWechat from '@/components/task/connect_wechat'
import confirmBox from '@/components/task/confirmBox'
import bindPhone from '@/components/common/bind_phone'
import { Indicator } from 'mint-ui'
import { mapState, mapMutations } from 'vuex'
import Vue from 'vue'
import clip from '@/assets/js/clipper.js'
import Calendar from 'vue2-datepick'
import ChooseAccount from '@/components/task/chooseAccount'

Vue.use(Calendar)
Vue.use(clip)
export default {
  computed: mapState({
    commonState: state => state.common
  }),
  data () {
    return {
      showChoose: false,
      dropBol: false,     // 
      headPort: false,
      configText: {},
      userName: false,
      genderToggle: false,
      birthdayToggle: false,
      succeed: false,
      showWechat: false, // 
      showConfirmBox: false, // 
      showSendCode: false, // 
      userInfo: {}, // 
      showBindPhone: false, // 
      errChange: 0,
      msgS: '',
      headerImage: '',
      picValue: '',
      dataTime: '',
      nicknames: '',
      changeName: '',
      imgSrc: '',    // 
      visible: false  // 
    }
  },
  components: {
    HeaderNavBar,
    ConnectWechat, // 
    confirmBox, // 
    bindPhone, // 
    ChooseAccount // 
  },
  methods: {
    ...mapMutations([
      'common_login_bol',
      'common_login_box',
      'common_index_column',
      'common_set_userInfo',
      'common_get_channel',
      'common_get_recondChannel',
      'set_shareType',
      'set_shareSuccessObj',
      'setShowShare',
      'login_bol'
    ]),
    change (event) {
      let image = document.getElementById('img') // 
      this.clip(event, {
        resultObj: image,
        aspectRatio: 1
      })
    },
    setDate () {
      this.$picker.show({
        type: 'datePicker',
        onOk: (date) => {
          if (new Date(date).getTime() > new Date('2010-01-01').getTime()) {
            this.msgS = '2010' // 
            this.hint()
          } else {
            this.$api.post('CHANGE_BIRTHDAY', {birthday: date}, res => {
              this.initData()
              this.msgS = '' // 
              this.hint()
            })
          }
        }
      })
    },
    // 
    jumpmygrade () {
      this.$router.push({name: 'Mygradeinfo'})
    },
    //  ()
    bindPhone () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
      if (!this.userInfo.mobile) {
        this.showBindPhone = true
      }
    },
    // 
    closeBindPhone (e) {
      if (e) {
        let data = {
          account: e.mobile,
          code: e.code
        }
        if (e.captcha_code) {
          data.captcha_code = e.captcha_code
        }
        this.$api.post('BIND_PHONE', data, res => {
          let shareSuccessObj = Object.assign({type: 5}, res.data)
          if (res.data.coin > 0) {
            this.set_shareSuccessObj({
              type: 7,
              shareSuccessObj
            })
            this.set_shareType(7)
            this.setShowShare(true)
          } else {
            this.$toast({
              message: '',
              duration: 1500
            })
          }
          this.showBindPhone = false
          this.getUserInfo()
        }, res => {
          if (res.code === 10) {
            this.errChange++
          } else {
            // this.showBindPhone = false
          }
        })
      } else {
        this.showBindPhone = false
      }
    },
    // 
    bindWechat () {
      // 
      if (!this.commonState.login_bol) {
        this.common_login_box(true)
        return
      }
      // 
      if (this.userInfo.wechat_nickname) {
        return
      }
      if (this.$config.source_style < 6) {
        if (this.$utils.isWx()) {
          this.showWechat = true
        } else {
          this.set_shareSuccessObj({
            type: '17',
            shareSuccessObj: {
              title: 'APPAPP',
              content: 'APP'
            }
          })
          this.set_shareType(17)
          this.setShowShare(true)
        }
      } else {
        this.showWechat = true
      }
    },
    // 
    getUserInfo () {
      this.$api.get('GET_USERINFO', {}, res => {
        // if (res.data.avatar.indexOf('default/avatar') > -1 && res.data.wechat_avatar) {
        //   res.data.avatar = res.data.wechat_avatar
        // }
        this.common_set_userInfo(Object.assign(this.commonState.userInfo, res.data))
        this.userInfo = res.data
        this.nicknames = this.userInfo.nickname
      })
    },
    // 
    closeWechat (e) {
      if (e) {
        if (this.$config.source_style >= 6) {
          this.appConfirm()
          // 
        } else {
          this.wechatConfirm()
        }
      } else {
        this.showWechat = false
      }
    },
    // 
    closeConfirmBox ({action}) {
      if (action === 'confirm') {
        this.showConfirmBox = false
        this.showChoose = true
      } else if (action === 'cancel') {
        this.showConfirmBox = false
        this.showWechat = false
      } else {
        this.showConfirmBox = false
        this.showWechat = false
      }
    },
    // 
    closeChoose ({action, type}) {
      if (action === 'confirm') {
        if (this.$config.source_style >= 6) {
          // app
          this.appConfirm({confirm: 1, retain_wechat: type})
        } else {
          // 
          this.wechatConfirm({confirm: 1, retain_wechat: type})
        }
      } else {
        this.showChoose = false
      }
    },
    // 
    wechatConfirm (data = {}) {
      Indicator.open('')
      setTimeout(() => {
        Indicator.close()
      }, 3000)
      data = Object.assign(data, {
        unionid: this.$utils.cache('unionid')
      })
      this.$api.post('WECHAT_BIND', data, res => {
        if (res.data.coin > 0) {
          let shareSuccessObj = Object.assign({type: 3, tipText: `${this.$utils.splitStr(res.data.wechat_nickname)}`}, res.data)
          this.set_shareSuccessObj({
            type: 16,
            shareSuccessObj
          })
          this.set_shareType(16)
          this.setShowShare(true)
        } else {
          this.$toast({
            message: `${this.$utils.splitStr(res.data.wechat_nickname)}`,
            duration: 1500
          })
        }
        this.showChoose = false
        this.showWechat = false
        this.showConfirmBox = false
        this.getUserInfo()
      }, res => {
        if (res.code === 801) {
          this.configText = res.data
          this.showWechat = false
          this.showSendCode = false
          this.showConfirmBox = true
        } else {
          this.showConfirmBox = false
        }
      })
    },
    // app
    appConfirm (data = {}) {
      // app
      let w = window.plus.nativeUI.showWaiting('')
      this.auths['weixin'].authorize(res => {
        w.close()
        data = Object.assign({type: 2}, data, {code: res.code})
        console.log('' + JSON.stringify(data))
        this.$api.post('APP_BIND', data, res => {
          if (res.data.coin > 0) {
            let shareSuccessObj = Object.assign({type: 3, tipText: `${this.$utils.splitStr(res.data.wechat_nickname)}`}, res.data)
            this.set_shareSuccessObj({
              type: 16,
              shareSuccessObj
            })
            this.set_shareType(16)
            this.setShowShare(true)
          } else {
            this.$toast({
              message: `${this.$utils.splitStr(res.data.wechat_nickname)}`,
              duration: 1500
            })
          }
          this.showChoose = false
          this.showWechat = false
          this.showConfirmBox = false
          this.getUserInfo()
        }, res => {
          if (res.code === 801) {
            this.configText = res.data
            this.showConfirmBox = true
            this.showWechat = false
          } else {
            this.$toast({
              message: res.msg,
              duration: 1500
            })
          }
        })
      }, () => {
        w.close()
        this.$toast({
          message: '',
          duration: 1500
        })
      })
    },
    back () {
      this.$router.go(-1)
    },
    showGender (type) {
      if (!this.commonState.login_bol) {
        this.common_login_box(true)
      } else {
        if (type === 'headPort') {
          this.headPort = true
        } else if (type === 'userName') {
          this.userName = true
        } else if (type === 'birthdayToggle') {
          this.birthdayToggle = true
          this.setDate()
        } else if (type === 'genderToggle') {
          this.genderToggle = true
        }
      }
    },
    // 
    gender (num) {
      if (num === this.userInfo.sex) {
        this.msgS = '' // 
        this.genderToggle = false // 
        this.hint()
      } else {
        this.$api.post('CHANGE_SEX', {sex: num}, res => {
          this.genderToggle = false
          this.initData()
          this.msgS = '' // 
          this.hint()
        })
      }
    },
    // name
    userNameS (nuber) {
      if (nuber === '') {
        this.msgS = '' // 
        this.hint()
      } else {
        this.$api.post('CHANGE_NICKNAME', {nickname: nuber}, res => {
          this.userName = false
          this.initData()
          this.msgS = '' // 
          this.hint()
          this.commonState.userInfo.nickname = nuber
          this.common_set_userInfo(this.commonState.userInfo)
        })
      }
    },
    // 
    newIMG (src) {
      this.$api.post('CHANGE_AVATAR', {avatar: src}, res => {
        this.initData()
        this.msgS = '' // 
        this.hint()
        this.commonState.userInfo.avatar = src
        this.common_set_userInfo(this.commonState.userInfo)
      })
    },
    getAuthFn () {
      this.$utils.plusReady(plus => {
        this.auths = {}
        plus.oauth.getServices(s => {
          for (let v of s) {
            this.auths[v.id] = v
          }
        })
      })
    },
    // 
    initData () {
      this.getUserInfo()
      this.getAuthFn()
    },
    hint () {
      this.succeed = true
      setTimeout(() => {
        this.succeed = false
      }, 2000)
    }
  },
  watch: {
    // 
    'commonState.login_box' (flag) {
      // 
      if (!flag && this.commonState.login_bol) {
        this.initData()
      }
    }
  },
  created () {
    this.initData()
  },
  mounted () {
  }
}
</script>
<style lang="scss" scoped>
