    position: relative;
    border-top: 1px solid #eee;
    height: 90px;
    line-height: 90px;
    padding: 0 30px;
    justify-content: space-between;
    .title-name {
      font-size: 32px;
    }
    &.border-bottom {
      border-bottom: 1px solid #eee;
    }
    &.getgrade {
      margin-top: 20.5px;
    }
    &.isWx.active{
      .text-right {
        .wechat_name {
          color: #4A90E2;
        }
        .arrowEl {
          border-color: #4A90E2;
        }
      }
    }
  }
  .icot{
    width: 14px;
    height: 25px;
    margin-left: 10px;
    display: inline-block;
    position: relative;
    .arrowEl {
      width: 16px;
      height: 16px;
      border-color: #999;
      margin-top: 1px;
    }
    // img{
    //   width: 100%;
    //   height: 100%;
    //   /*width: 14px;*/
    //   /*height: 25px;*/
    //   position: absolute;
    //   right: 0;
    //   top: 0;
    // }
  }
  .item-xian {
    color: #999;
    white-space: nowrap;
    position: relative;
    width: 65%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    font-size: 32px;
  }

  .headPort >div,.genderToggle>div{
    height: 80px;
    line-height: 80px;
    background: white;
  }
  .headPort,.genderToggle {
    position: fixed;
    bottom: -280px;
    width: 100%;
    z-index: 50;
  }
  .genderToggle {
    font-size: 30px;
    box-shadow: 0 -2px 8px 0 rgba(135,134,134,0.50);
    border-radius: 10px 10px 0 0;
    overflow: hidden;
    color: #368ECA;
    .photograph {
      border-top: 1px solid #DBDBDB;
      height: 88px;
      box-sizing: border-box;
      border-bottom: 1px solid #dbdbdb;
    }
